<template>
  <div class="row mt-3 row-all">
    <div class="col-md-6 box-all" v-for="product in products" :key="product.id">
      <div class="box-product">
        <div class="row mt-3">
          <div class="col-md-6 center-all">
            <img class="w-100" :src="product.img" alt="">
          </div>
          <div class="col-md-6 ">
            <h3>{{product.name}}</h3>
            <p>{{product.price}} </p>
            <p class="more"> >></p>
          
            <div class="center-all mb-3  mt-3">
             
              <ButtonLink @customEvent="addToCart(product)" text="  " link="/store" />
              
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  // @ is an alias to /src
  // import OnNapoli from '@/components/OnNapoli.vue'


  export default {
    name: 'Store',
    components: {

    },
    data() {
      return {

      }
    },
    methods: {
      // addPrice(product) {
      //   product.amount++
      // },
      // minusPrice(product) {
      //   if (product.amount > 0) {
      //     product.amount--
      //   }
      // },
      addToCart(product) {
        product.amount++
        this.$store.commit('allTimeShowCart')
      }
    },
    computed: {
      products() {
        return this.$store.state.products
      }
    },
  }
</script>

